.btn {
    cursor: pointer;
    border-radius: 10px;
    color: #fff;
    border: none;
    font-weight: 650;
}
.btn-blue {
    background-color: #0063F7;
}
.btn-orange {
    background-color: #FFA238;
    color: #000;
    border: #000 1px solid;
}
.btn-gray {
    background-color: #F8F8F8;
    color:#000;
}
.btn-cart-added {
    background-color: #06C270;
    font-size: 11px;
}
.btn-card {
    font-size: 12px;
    background-color: #FD7E14;
    font-size: 11px;
}
.btn-search {
    width: 170px;
    height: 40px;
    font-size: 16px;
}
.btn-cta {
    height: 36px;
    padding: 8px 24px;
    font-size: 16px;
    border-radius: 20px;
    border: 0.5px solid #000;
    background: var(--Primary-CTA-hover, #5B8DEF);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.btn-action-chip {
    height: 48px;
    padding-right: 5px;
    padding-left: 5px;
    font-size: 16px;
}
.btn-x-small {
    width: 98px;
    height: 24px;
}
.btn-small, .btn-small-navbar {
    width: 150px;
    height: 40px;
    font-size: 16px;
}
.btn-short {
    width: 167px;
    height: 37px;
    font-size: 16px;
}
.btn-medium {
    width: 318px;
    height: 40px;
    font-size: 16px;
}
.btn-large {
    width: 374px;
    height: 40px;
    font-size: 16px;
}
.btn-orange:hover {
    background-color: #FFBB3F;
    transition: all 0.3s ease-out;
}
.btn-blue:hover {
    background-color: #5B8DEF;
    transition: all 0.3s ease-out;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.btn-gray:hover {
    background-color: #0063F7;
    color:#fff;
    transition: all 0.3s ease-out;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
    border:#fff 1px solid;
}
.btn-gray .black-icon {
    color: #000000;
}
.btn-gray:hover .black-icon {
    color: #fff;
}
.btn-blue:invalid {
    background-color: #CCDDFA;
    transition: all 0.3s ease-out;
}
.btn-footer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 31px;
    width: 200px;
    background-color: #9DBFF9;
    color:#000;
    font-weight: 650;
    border: none;
    font-size: 14px;
}
@media screen and (max-width: 1250px) {
    .btn-cta {
        background-color: #fff;
        color: #456FAA;
        border: none;
        box-shadow: none;
        height: 28px;
        width: 100px;
        padding: 2px 8px;
        transition: transform 0.5s ease, font-size 0.5s ease;
        font-size: 14px;
        display: inline-block;
   }
    .btn-cta:hover {
        transform: scaleX(1.2);
        font-size: 16px;
   }
    .cta-text {
        display: none;
   }
    .btn-cta::before {
        content: "Shop Now";
   }
}
@media screen and (max-width: 1160px) {
    .btn-card {
        width: 30px;
        height: 30px;
        border-radius: 50px;
        background: url('../media/basketorange.png') center/contain no-repeat;
   }
    .btn-cart-added {
        width: 30px;
        height: 30px;
        border-radius: 50px;
        background-image: url('../media/basketgreen.png');
        background-size: contain;
        background-repeat: no-repeat;
   }
    .card-button-text {
        display: none;
   }
}
@media screen and (max-width: 820px) {
    .btn-footer {
        position: relative;
        width: 130px;
        display: flex;
        justify-content: center;
        align-items: center;
   }
    .btn-small {
        width: 40px;
        height: 40px;
        border: none;
        background-color: #0D4F77;
   }
    .btn.btn-no-hover .icon {
        color: #000000;
   }
    .white-icon {
        color: #ffffff;
   }
    .btn.btn-no-hover:hover {
        transition: none;
        box-shadow: none;
        border:none;
        background-color: #0D4F77;
   }
    .btn-gray {
        width: 40px;
        background-color: #D9D9D9;
   }
    .button-text {
        display: none;
   }
    .btn-large::before, .btn-medium::before {
        content: "See More";
   }
    .btn-large, .btn-medium {
        width: 120px;
   }
    .btn-small-navbar {
        width: 20px;
        height: 20px;
        border: none;
        background-color: #0D4F77;
   }
    .btn.btn-no-hover .icon {
        color: #000000;
   }
    .white-icon {
        color: #ffffff;
   }
    .btn.btn-no-hover:hover {
        transition: none;
        box-shadow: none;
        border:none;
        background-color: #0D4F77;
   }
    .button-text-navbar {
        display: none;
   }
}
